<template>
    <div class="Index">
        <div class="layout">
            <img src="../assets/images/logo.jpg"
                 alt="">
            <div class="search">
                <el-input placeholder="请输入内容"
                          v-model="val">
                </el-input>
                <el-button icon="el-icon-search"
                           @click="go"></el-button>
            </div>
            <el-radio-group v-model="radio">
                <el-radio :label="1">题目</el-radio>
                <el-radio :label="2">作者</el-radio>
                <el-radio :label="3">摘要</el-radio>
                <el-radio :label="4">正文</el-radio>
                <el-radio :label="5">全局检索</el-radio>
            </el-radio-group>
        </div>
        <footer>
            Copyright © 2020 solar. All Rights Reserved. solar 版权所有
        </footer>
    </div>
</template>

<script>
export default {
	name: 'Index',
	data() {
		return {
			val: '',
			radio: 5,
		}
	},
	components: {},
	methods: {
		go() {
			if (this.val !== '') {
				this.$router.push({
					path: '/content',
					query: {
						val: this.val,
						radio: this.radio,
					},
				})
			}
		},
	},
}
</script>

<style lang="scss">
@import '../assets/css/_mixin.scss';
.Index {
	width: 100%;
	height: 100%;
	overflow: hidden;
	.layout {
		width: 600px;
		margin: 100px auto;
		@include box_flex;
		@include direction(column);
		@include align;
		overflow: hidden;
		height: 400px;
		.search {
			width: 100%;
			@include box_flex;
			margin-top: 50px;
			@include justify(space-between);
			.el-input {
				margin-right: 10px;
			}
		}
		.el-radio-group {
			width: 80%;
			height: 30px;
			margin-top: 20px;
			@include box_flex;
			@include align;
		}
	}
	footer {
		width: 100%;
		height: 40px;
		position: absolute;
		left: 0;
		bottom: 0;
		text-align: center;
	}
}
</style>



